import React from 'react'
import 'isomorphic-unfetch'

export default class VoIPViewer extends React.Component {
  async componentDidMount () {
    // eslint-disable-next-line no-undef
    // const res = await fetch('https://www.nfoservers.com/query/mstat.pl?id=pktloss')
    // const json = await res.json()

    this.setState(state => ({
      rootChannel: {
        name: "Root",
        users: ["Dark_Arc"],
        channels: [
          {
            name: "Game Lobby 1",
            users: ["Cow_Fu"],
            channels: []
          }
        ]
      }
    }));
  }

  renderChannel = (channelData, level) => {
    let channelStyle = {
      marginLeft: level * 20 + 'px'
    }

    let channelName = <span><span className="oi oi-icon-name" title="icon name" aria-hidden="true"></span>{channelData.name}</span>

    let users = []
    channelData.users.forEach((user) => {
      users.push(<div>{user}</div>);
    })

    let subChannels = []
    channelData.channels.forEach((channel) => {
      subChannels.push(this.renderChannel(channel, level + 1))
    })

    return (
      <div style={channelStyle}>
        {channelName}
        {users}
        {subChannels}
      </div>
    )
  }


  render() {
    let channelPreview

    if (this.state && this.state.rootChannel) {
      channelPreview = this.renderChannel(this.state.rootChannel, 0)
    } else {
      channelPreview = <div>Loading...</div>
    }

    return (
      <div>
        {channelPreview}
      </div>
    )
  }
}

